const mongoose = require('mongoose');
import autoIncrement from 'mongoose-auto-increment';
const { ObjectId } = mongoose.Schema;

const SimSimRoomSchema = mongoose.Schema({
    title: {
        type: String,
    },
    user: {
        type: ObjectId,
        ref: 'User',
    },

    date: {
        type: Date,
        default: Date.now(),
    },
});

autoIncrement.initialize(mongoose.connection);
SimSimRoomSchema.plugin(autoIncrement.plugin, {
    model: 'SimSimRoom',
    field: 'id',
    startAt: 1,
    incrementBy: 1,
    index: true,
});

const model = mongoose.model('SimSimRoom', SimSimRoomSchema);

export default model;
